import * as React from 'react';
import { TweenLite } from 'gsap';

class Engine {
  private pieces: Array<0 | 1 | 2>; // 0 is empty, 1 is red, 2 is yellow
  private isRedToMove: boolean;

  constructor() {
    this.pieces = new Array(6 * 7);
    this.pieces.fill(0);
    this.isRedToMove = true;
  }

  makeMove = (columnIndex: number) => {
    if (columnIndex < 0) { return null; }
    if (columnIndex > 6) { return null; }

    let rowIndex;
    for (rowIndex = 0; rowIndex < 6; ++rowIndex) {
      const pieceIndex = rowIndex * 7 + columnIndex;
      if (this.pieces[pieceIndex] === 0) {
        const isRed = this.isRedToMove;
        this.pieces[pieceIndex] = isRed ? 1 : 2;
        this.isRedToMove = !this.isRedToMove;
        return { rowIndex, isRed };
      }
    }
    return null;
  }
}

export default class Board extends React.PureComponent<{}, { selectedColumnIndex: number }> {
  private static DISPLAY_WIDTH = 350;
  private pieces: Array<(SVGCircleElement | null)>;
  private engine: Engine;

  constructor(props: {}) {
    super(props);
    this.state = {
      selectedColumnIndex: -1
    };
    this.pieces = new Array<(SVGCircleElement | null)>();
    this.engine = new Engine();
  }

  calculateColumnIndex = (e: React.MouseEvent<SVGSVGElement>) => (
    Math.floor(7 * (e.clientX - (window.innerWidth - Board.DISPLAY_WIDTH) / 2) / Board.DISPLAY_WIDTH)
  )

  handleMouseDown = (e: React.MouseEvent<SVGSVGElement>) => {
    const columnIndex = this.calculateColumnIndex(e);
    const legalMove = this.engine.makeMove(columnIndex);
    if (!legalMove) {
      return;
    }
    const { rowIndex, isRed } = legalMove;
    let pieceToTween = this.pieces[rowIndex * 7 + columnIndex];
    TweenLite.to(pieceToTween, 0.4, { y: 100 * (6 - rowIndex), fill: isRed ? '#FA4A2A' : '#FFE769' });
    // TODO: generate computer's reply here, e.g. engine.getBestMove()
  }

  handleMouseMove = (e: React.MouseEvent<SVGSVGElement>) => {
    this.setState({ selectedColumnIndex: this.calculateColumnIndex(e) });
  }
  
  handleMouseOut = () => {
    this.setState({ selectedColumnIndex: -1 });
  }

  render() {
    const { selectedColumnIndex } = this.state;
    return (
      <svg
        width={Board.DISPLAY_WIDTH}
        viewBox="0 0 700 700"
        xmlns="http://www.w3.org/2000/svg"
        onMouseMove={this.handleMouseMove}
        onMouseOut={this.handleMouseOut}
        onMouseDown={this.handleMouseDown}
      >
        <defs>
          <pattern id="cell-pattern" patternUnits="userSpaceOnUse" width="100" height="100">
            <circle  cx="50" cy="50" r="45" fill="black" />
          </pattern>
          <mask id="cell-mask">
            <rect width="100" height="600" fill="white" />
            <rect width="100" height="600" fill="url(#cell-pattern)" />
          </mask>
        </defs>
        <svg
          x={100 * selectedColumnIndex}
          y="0px"
          width="100px"
          height="50px"
          viewBox="0 0 284.929 284.929"
          fill="lime"
        >
          <g>
            <path 
              d="M282.082,76.511l-14.274-14.273c-1.902-1.906-4.093-2.856-6.57-2.856c-2.471,
              0-4.661,0.95-6.563,2.856L142.466,174.441 L30.262,
              62.241c-1.903-1.906-4.093-2.856-6.567-2.856c-2.475,0-4.665,0.95-6.567,2.856L2.856,
              76.515C0.95,78.417,0,80.607,0,83.082 c0,2.473,0.953,4.663,2.856,6.565l133.043,
              133.046c1.902,1.903,4.093,2.854,6.567,2.854s4.661-0.951,6.562-2.854L282.082,89.647
              c1.902-1.903,2.847-4.093,2.847-6.565C284.929,80.607,283.984,78.417,282.082,76.511z"
            />
          </g>
        </svg>
        {(() => {
          const columns = new Array();
          for (let column = 0; column < 7; ++column) {
            columns.push(
              <svg x={100 * column} y={50} key={`column_${column}`}>
                {(() => {
                  const svgChildren = new Array();
                  for (let row = 0; row < 6; ++row) {
                    const index = column + row * 7;
                    svgChildren.push(
                      <circle
                        cx="50"
                        cy="-50"
                        r="45"
                        fill="#FA4A2A"
                        key={`circle_${index}`}
                        ref={e => this.pieces[index] = e}
                      />,
                      <rect
                        width="100"
                        height="600"
                        fill="#6281FE"
                        mask="url(#cell-mask)"
                        key={`rect_${index}`}
                      />
                    );
                  }
                  return svgChildren;
                })()}
              </svg>
            ); 
          }
          return columns;
        })()}
      </svg>
    );
  }
}



